import React, { Component } from 'react';
import './Listing.css';

class Listing extends Component {
  render() {
    const foodTruckUrl = 'https://data.sfgov.org/resource/rqzj-sfat.json';
    let foodTruckName;

    function displayFoodTruck(responseJson) {
      console.log('showin food trucks!', responseJson[0].applicant);
      foodTruckName = responseJson[0].applicant;
    }

    fetch(foodTruckUrl)
      .then((response) => {
        return response.json();
      })
      .then((responseJson) => displayFoodTruck(responseJson))
      .catch((error) => console.log('Food trucks not found, sorry!'));

    return (
      <div className='listing'>
        <h1 className='listingTitle'>Paco's Tacos!</h1>
        <p className='listingAddress'>{foodTruckName}</p>
      </div>
    );
  }
}

export default Listing;
